<template>
  <Scrollable class="thumb-picker" :class="{x, y}"
    :y="y" :x="x" style="flex: 1" :outsideScrollbar="true" v-if="collocation">
    <a class="item"
      v-if="collocation.threeDimensional"
      :class="{current: isShowKeyframe}" @click="changeKeyframe">
      <BaseImage class="base-image"
        :src="collocation.contentThumbnails[0]" fit="contain">
        <template v-slot:error>
          <div class="img-error">
            <img src="@/assets/img/public/default-image.png"
              alt="error" style="width: 100%;height:100%" />
          </div>
        </template></BaseImage>
      <div class="btn-view-360">
        <i class="iconfont icon-rotate-360" />
      </div>
    </a>
    <a v-for="item in collocation.contentThumbnails"
      @click="changePreview(item)"
      :key="item" class="item" :class="{current: preview === item && !isShowKeyframe}">
      <BaseImage class="base-image" :src="item" fit="contain">
        <template v-slot:error>
          <div class="img-error">
            <img src="@/assets/img/public/default-image.png"
              alt="error" style="width: 100%;height:100%" />
          </div>
        </template></BaseImage>
    </a>
  </Scrollable>

</template>
<style scoped lang="scss">
.thumb-picker {
  &.x {
    height: 58px;
  }
  /deep/ .scrollable-x {
    display: flex;
    justify-content: center;
  }
  .btn-view-360 {
    top: 1px;
    width: 52px;
    height: 52px;
    position: absolute;
    background: rgba(0, 0, 0, 0.25);
  }
  .icon-rotate-360 {
    position: absolute;
    top: 50%;
    left: 50%;
    font-size: 40px;
    color: white;
    transform: translate(-50%, -50%);
  }
  .item {
    position: relative;
    cursor: pointer;
    border: 2px solid transparent;
    display: block;
    padding: 1px;
    font-size: 0;
    height: 58px;
    > .base-image {
      width: 52px;
      height: 52px;
    }
    &.current {
      $current-color: black;
      border-color: $current-color;
      border-width: 2px;
    }
  }
}
</style>
<script>
import Vue from 'vue';
import BaseImage from '@/components/base/Image';
import Scrollable from '@/components/Scrollable';

export default Vue.extend({
  components: {
    BaseImage,
    Scrollable,
  },
  props: {
    x: Boolean,
    y: {
      type: Boolean,
      value: true,
    },
    collocation: Object,
    isShowKeyframe: Boolean,
    preview: String,
  },
  methods: {
    changePreview(preview) {
      this.$emit('update:isShowKeyframe', false);
      this.$emit('update:preview', preview);
    },
    changeKeyframe() {
      this.$emit('update:isShowKeyframe', true);
    },
  },
});

</script>
