<template>
  <column flex="1" class="order-metting-detail">
    <row flex="1" v-loading="!appInfo" class="detail-main">
      <template v-if="appInfo">
        <column class="detail-left">
          <!-- 姓名、价格和加入心愿单 -->
          <DetailHeader v-bind="{appInfo, collocation}" />

          <row flex="1">
            <!-- sku选择（颜色款式选择） -->
            <column class="sku-picker">
              <Scrollable y style="flex: 1" :outsideScrollbar="true"
                ref="skuPickerScrollable"
                @scroll="(e, scrollData) => {
                  skuPickerCanScrollDown = scrollData.canScrollDown;
                  skuPickerCanScrollUp= scrollData.canScrollUp
                  }"
                @inited="(scrollData) => {
                  skuPickerCanScrollDown = scrollData.canScrollDown;
                  skuPickerCanScrollUp= scrollData.canScrollUp
                }">
                <a v-for="item in appInfo.collocationList"
                  :key="item.id" class="item"
                  @click="changeCollocation(item)"
                  :class="{current: collocation === item}">
                  <AspectWidth :aspect="1">
                    <BaseImage class="base-image" :src="item.contentThumbnails[0]" fit="contain">
                      <template v-slot:error>
                        <div class="img-error">
                          <img src="@/assets/img/public/default-image.png"
                            alt="error" style="width: 100%;height:100%" />
                        </div>
                      </template>
                    </BaseImage>
                  </AspectWidth>
                </a>
              </Scrollable>
              <a class="btn-scroll"
                v-if="skuPickerCanScrollDown"
                @click="$refs.skuPickerScrollable.scroll()">
                <i class="iconfont icon-angle-down" />
              </a>
              <a class="btn-scroll" v-else-if="!skuPickerCanScrollDown && skuPickerCanScrollUp"
                @click="$refs.skuPickerScrollable.scrollToStart()">
                <i class="iconfont icon-angle-up" />
              </a>
            </column>
            <!-- 鞋款下的方向选择 -->
            <ThumbPicker :collocation="collocation"
              :isShowKeyframe.sync="isShowKeyframe"
              :preview.sync="preview" class="thumb-picker" />
          </row>
        </column>
        <!-- 选择器和大图 -->
        <div class="shoe-viewer">
          <div class="indicator">{{ skuIndex + 1 }}/{{ skuList.length }}</div>
          <a class="btn-arrow-left" @click="$act(clickFocusModeNext, [true])"
            v-if="prevShoeAndCollocation">
            <i class="iconfont icon-left-arrow-thin" /></a>
          <a class="btn-arrow-right" @click="$act(clickFocusModeNext, [])"
            v-if="nextShoeAndCollocation">
            <i class="iconfont icon-right-arrow-thin" /></a>
          <div class="btn-group top-right">
            <a class="btn-add-to-wish"
              :data-title="$root.isFullscreen ?
              $t('order-meeting.detail.exit_fullscreen_mode')
              : $t('order-meeting.detail.fullscreen_mode')"
              v-if="!isQt()"
              @click="toggleFullscreen">
              <i class="iconfont"
                :class="$root.isFullscreen ? 'icon-focus-mode' : 'icon-focus-mode-o'" />
            </a>
            <!--
              v-if="(canAddWish === undefined ? false : canAddWish) &&
              $can('system', 'OP0148')"
            -->
            <a class="btn-add-to-wish"
              v-if="canAddWish === undefined ? false : canAddWish"
              :data-title="$t('order-meeting.detail.add_to_order')"
              @click="$act(addWish, [])">
              <i class="iconfont icon-wish-list-o" />
            </a>
          </div>
          <div class="btn-group bottom-right">
            <a class="btn-3d" :data-title="$t('order-meeting.detail.3d_mode')"
              v-if="isQt() && currentShoeOfShoeList &&currentShoeOfShoeList.uploadStatus"
              @click="handleOpenRenderer">
              <i class="iconfont icon-3d" /></a>
          </div>

          <ThumbPicker :collocation="collocation"
            :isShowKeyframe.sync="isShowKeyframe"
            :preview.sync="preview" class="thumb-picker" :x="true" :y="false" />
          <DetailHeader class="detail-header" v-bind="{appInfo, collocation}" />
          <GestureFrame style="flex: 1;"
            v-if="keyframe"
            class="gesture-frame"
            :style="isShowKeyframe ? {zIndex: 1 } : { opacity: 0 }"
            :speed="[-9 / 375, -9 / 375]"
            v-model="keyframe.value" :items="keyframe.items" />
          <div
            class="preview"
            @click="({target})=>{
                $ImageViewer.show(
                  collocation.contentThumbnails.map(item=>({src: item})),
                  { index: collocation.contentThumbnails.indexOf(preview),
                    targetElems: [target]
                  }
                )}"
            :style=" [
                { backgroundImage: `url('${previewWithSign}')` },
                !isShowKeyframe ? {zIndex: 1 } : { opacity: 0 }]" />
        </div>
        <!-- 右侧，包括鞋款信息和评论 -->
        <column class="shoe-detail">
          <row class="tabs flex-center">
            <div style="flex:1;"
              :class="{current: isShowComment === true}" @click="isShowComment = true">
              <a>{{$t('order-meeting.detail.my_comments')}}</a>
            </div>
            <div style="flex:1;"
              :class="{current: isShowComment === false}" @click="isShowComment = false">
              <a>{{$t('order-meeting.detail.product-info')}}</a>
            </div>

          </row>

          <Comment v-bind="{ objectId: collocation.id, workspaceId }"
            style="flex:1" v-if="isShowComment" />
          <div style="flex: 1" class="shoe-extra" v-else>
            <div class="inspiration"
              v-if="appInfo.shoeInfoExtraField.inspiration"
              v-html="lf2p(appInfo.shoeInfoExtraField.inspiration)" />
            <dl>
              <dt>{{$t('order-meeting.detail.materials')}}</dt>
              <dd>{{appInfo.shoeInfoExtraField.materials}}</dd>
              <dt>{{$t('order-meeting.detail.last_width')}}</dt>
              <dd>{{appInfo.shoeInfoExtraField.width}}</dd>
              <dt>{{$t('order-meeting.detail.weight')}}</dt>
              <dd>{{appInfo.shoeInfoExtraField.weight}}</dd>
              <dt>{{$t('order-meeting.detail.dimensions')}}</dt>
              <dd>{{appInfo.shoeInfoExtraField.dimensions}}</dd>
              <dt>{{$t('order-meeting.detail.size-run')}}</dt>
              <dd><span v-for="item in appInfo.sizes" :key="item" class="size">{{item}}</span></dd>
            </dl>
          </div>
        </column>
      </template>

    </row>
    <row class="shoe-picker">
      <template v-if="shoeList">

        <!-- <a class="btn-prev" @click="clickNextCollection(true)" v-if="prevCollection">
          <i class="el-icon-arrow-left"></i>
        </a> -->
        <ProductList style="flex: 1"
          :items="shoeList" size="sm" :value="currentShoeOfShoeList"
          @clickItem="(item) => clickItem(item, item.fileInfo.shoeCollocationInfoList[0])" />
        <!-- <a class="btn-next" @click="clickNextCollection()" v-if="nextCollection">
          <i class="el-icon-arrow-right"></i>
        </a> -->
      </template>

    </row>
  </column>
</template>
<script>
import Vue from 'vue';
import i18n from 'i18next';
import GestureFrame from '@/components/GestureFrame';
import Scrollable from '@/components/Scrollable';
import BaseImage from '@/components/base/Image';
import AspectWidth from '@/components/AspectWidth';
import Comment from '@/views/model/detail/comment/indexFilaVersion';
import generateManyDimensionsArray from '_/generate-many-dimensions-array';
import { getVersionDetail } from '@/api/shoe';
import toast from '@/components/ToastTipWrapper';
import {
  getCustomizedAppInfoByRelateId, addWish, isAvailableAddingWish,
} from '@/api/order-meeting';
import { exec } from '@/utils/exec';
import { lf2p } from '@/filters';
import { signUrl } from '@/utils/oss/utils';
import ProductList from '../ProductList';
import { getFileRelateIdByCatalogId } from '../../../api/catalogue';
import ThumbPicker from './ThumbPicker';
import DetailHeader from './Header';
/** @typedef {import('@/api/order-meeting').CustomizedAppInfo} CustomizedAppInfo */
/** @typedef {import('@/api/order-meeting').CatalogForTradeFair} CatalogForTradeFair */
/** @typedef {import('@/api/order-meeting').Collocation} Collocation */
/** @typedef {{relateId: string}} VersionOnlyWithRelateId */
// /** @typedef {import('@/api/shoe').Version} Version */
/** @typedef {VersionOnlyWithRelateId} Version */
/** @typedef {{items: string[], value: [number, number], maxScale: number}} GestureFrameOptions */

/**
 *
 * @param {Collocation} collocation
 * @returns {GestureFrameOptions}
 */
function initGestureFrame(collocation) {
  const yLength = collocation.threeDimensional.keyframeYaxisLength;
  const xLength = collocation.threeDimensional.keyframeXaxisLength;
  const imgs360 = generateManyDimensionsArray(
    [yLength, xLength],
    ds => {
      return {
        // url: `${collocation.resourceUrl}/360/${ds[0] * xLength + ds[1] + 1}.jpg`,
        url: `${collocation.resourceUrl}/360/${ds[0]}_${ds[1]}.jpg`,
      };
    },
  );
  const valueY = Math.floor(imgs360.length / 2);
  const valueX = Math.floor(imgs360[0].length / 2);
  return {
    items: imgs360,
    value: [valueY, valueX],
    maxScale: 3,
  };
}

export default Vue.extend({
  props: {
    catalogId: {
      type: String,
    },
    workspaceId: {
      type: String,
    },
    collocationId: {},
    collection: {
      type: String,
    },
    /** 这两个用于切换系列 */
    collections: {},
    allShoeList: {},
  },
  components: {
    DetailHeader,
    ThumbPicker,
    AspectWidth,
    Scrollable,
    BaseImage,
    ProductList,
    GestureFrame,
    Comment,
  },
  inject: ['isQt'],
  methods: {
    toggleFullscreen() {
      this.skuIndex = this.skuList.findIndex(sku => sku.id === this.collocation.id);
      this.$act(this.$root.toggleFullscreen, []);
    },
    getNextShoeAndCollocation(prev) {
      /** @type {CustomizedAppInfo} */
      const { appInfo } = this;
      const { collocationIndex } = this;
      const lastCollocationIndex = appInfo.collocationList.length - 1;
      const shoeIndex = this.allShoeList.indexOf(this.currentShoeOfShoeList);
      const lastShoeIndex = this.allShoeList.length - 1;
      /** @type {CatalogForTradeFair} */
      let targetShoe;
      /** @type {Collocation} */
      let targetCollocation;
      if (prev) {
        if (collocationIndex === 0) {
          if (shoeIndex) {
            targetShoe = this.shoeList[shoeIndex - 1];
            const tmp = targetShoe.fileInfo.shoeCollocationInfoList;
            targetCollocation = tmp[tmp.length - 1];
          }
        } else {
          targetShoe = this.currentShoeOfShoeList;
          const tmp = targetShoe.fileInfo.shoeCollocationInfoList;
          targetCollocation = tmp[collocationIndex - 1];
        }
      } else if (collocationIndex === lastCollocationIndex) {
        if (shoeIndex !== lastShoeIndex) {
          targetShoe = this.shoeList[shoeIndex + 1];
          const tmp = targetShoe.fileInfo.shoeCollocationInfoList;
          [targetCollocation] = tmp;
        }
      } else {
        targetShoe = this.currentShoeOfShoeList;
        const tmp = targetShoe.fileInfo.shoeCollocationInfoList;
        targetCollocation = tmp[collocationIndex + 1];
      }
      if (targetCollocation) {
        return {
          shoe: targetShoe,
          collocation: targetCollocation,
        };
      }
      return undefined;
    },
    clickFocusModeNext(prev) {
      const s = prev
        ? this.prevShoeAndCollocation : this.nextShoeAndCollocation;
      if (s) {
        const { shoe, collocation } = s;
        if (collocation) {
          this.clickItem(shoe, collocation);
          this.skuIndex = this.skuList.findIndex(sku => sku.id === collocation.id);
        }
      }
    },
    clickItem(targetShoe, targetCollocation) {
      if (!targetCollocation) {
        const tmp = targetShoe.fileInfo.shoeCollocationInfoList;
        targetCollocation = tmp[tmp.length - 1];
      }
      this.currentShoeOfShoeList = targetShoe;
      this.$router.push({
        path: '/order-meeting/detail',
        query: {
          catalogId: targetShoe.id,
          workspaceId: this.workspaceId,
          collection: targetShoe.fileInfo.collection,
          collocationId: targetCollocation.id,
        },
      });
    },
    changeCollocation(collocation) {
      this.$router.push({
        path: '/order-meeting/detail',
        query: {
          catalogId: this.catalogId,
          workspaceId: this.workspaceId,
          collection: this.collection,
          collocationId: collocation.id,
        },
      });
    },
    async changePreview(preview) {
      this.isShowKeyframe = false;
      this.preview = preview;
    },
    clickKeyframe() {
      this.isShowKeyframe = true;
    },
    lf2p,
    async addWish() {
      // try {
      await addWish({
        catalogId: this.catalogId,
        collection: this.collection,
        shoeSize: this.appInfo.size,
        shoeName: this.appInfo.styleName,
        barCode: this.appInfo.barCode,
        curtailImg: this.collocation.contentThumbnails[0],
        type: this.collection,
        colorName: this.collocation.color,
        colorId: this.collocationId,
        shoeNumber: 1,
      }, this.workspaceId);
      toast('icon-wish-list-o', i18n.t('order-meeting.detail.added_to_order')).open();
      // this.$emit('add-to-wish');
      // } catch (e) {
      //   console.log(e);
      //   this.$message.error('已经提交过了，无法提交');
      // }
    },
    async clickNextCollection(prev) {
      const nextCollection = prev ? this.prevCollection : this.nextCollection;
      const shoeList = this.allShoeList.filter(it => it.fileInfo.collection === nextCollection);
      /** @type {CatalogForTradeFair} */
      const nextShoe = shoeList[prev ? shoeList.length - 1 : 0];
      this.$router.push({
        query: {
          catalogId: nextShoe.id,
          workspaceId: this.workspaceId,
          collocationId: nextShoe.fileInfo.shoeCollocationInfoList[0].id,
          collection: nextShoe.fileInfo.collection,
        },
      });
      toast(
        'icon-double-ink',
        i18n.t(
          'order-meeting.detail.jump_to_series',
          [!prev ? i18n.t('order-meeting.detail.next') : i18n.t('order-meeting.detail.previous')],
        ),
      ).open();
    },
    async handleOpenRenderer() {
      const {
        relateId, relateType, id, uploadStatus, name,
      } = this.currentShoeOfShoeList;
      // console.log(relateId, relateType, id, uploadStatus, name);
      await exec('openRenderer', {
        relateId,
        relateType,
        id,
        workspaceId: this.workspaceId,
        uploadStatus,
        name,
      });
    },
  },
  computed: {
    collocationIndex() {
      return this.appInfo.collocationList.indexOf(this.collocation);
    },
    nextShoeAndCollocation() {
      return this.getNextShoeAndCollocation();
    },
    prevShoeAndCollocation() {
      return this.getNextShoeAndCollocation(true);
    },
    indexOfCurrentCollection() {
      return this.collections.indexOf(this.collection);
    },
    prevCollection() {
      const idx = this.collections.indexOf(this.collection);
      return idx !== 0;
    },
    nextCollection() {
      const idx = this.collections.indexOf(this.collection);
      return idx !== this.collections.length - 1;
    },
  },
  mounted() {
    // 全部鞋款的sku列表（不包括鞋系列层级）
    this.allShoeList.forEach(series => {
      series.fileInfo.shoeCollocationInfoList.forEach(sku => this.skuList.push(sku));
    });
  },
  async created() {
    (async () => {
      this.canAddWish = (await isAvailableAddingWish(this.workspaceId)).data;
    })();
    this.$watch(() => {
      return {
        workspaceId: this.workspaceId,
        catalogId: this.catalogId,
      };
    }, async ({ workspaceId: _workspaceId, catalogId: _catalogId }) => {
      this.version = undefined;
      this.version = {
        relateId: (await getFileRelateIdByCatalogId({
          groupAppId: _workspaceId,
          catalogId: _catalogId,
          relateType: 0,
        })).data,
      };
      // this.version = (await getVersionDetail(_catalogId, '', _workspaceId)).data;
    }, {
      immediate: true,
    });

    this.$watch(() => {
      return {
        // workspaceId: this.workspaceId,
        allShoeList: this.allShoeList,
        collection: this.collection,
      };
    }, async ({
      // workspaceId: _workspaceId,
      // collection: _collection,
      allShoeList: _allShoeList,
    }) => {
      this.shoeList = undefined;
      // this.shoeList = (await getTradeFairList(_workspaceId, [_collection])).data;
      // if (_allShoeList.length) {
      //   this.shoeList = _allShoeList.filter(it => it.fileInfo.collection === _collection);
      // }
      this.shoeList = _allShoeList;
    }, {
      immediate: true,
    });

    this.$watch(() => {
      return {
        version: this.version,
        shoeList: this.shoeList,
      };
    }, ({ version: _version, shoeList: _shoeList }) => {
      // this.currentShoeOfShoeList = undefined;
      this.isShowComment = true;
      if (_version && _shoeList) {
        this.currentShoeOfShoeList = _shoeList.find(shoe => shoe.relateId === _version.relateId);
      }
    }, {
      immediate: true,
    });

    this.$watch(() => {
      return {
        appInfo: this.appInfo,
        collocationId: this.collocationId,
      };
    }, ({ appInfo: _appInfo, collocationId: _collocationId }) => {
      this.collocation = undefined;
      this.isShowKeyframe = false;
      if (!_appInfo) return;
      [this.collocation] = _appInfo.collocationList;
      if (_collocationId) {
        this.collocation = _appInfo.collocationList
          .find(collocation => collocation.id === _collocationId);
      } else {
        [this.collocation] = _appInfo.collocationList;
      }
      console.info(this.collocation);
      if (this.collocation?.threeDimensional) {
        this.isShowKeyframe = true;
      } else {
        this.preview = undefined;
      }
    });

    this.$watch(() => {
      return {
        workspaceId: this.workspaceId,
        version: this.version,
      };
    }, async ({ workspaceId: _workspaceId, version: _version }) => {
      this.appInfo = undefined;
      if (_version) {
        this.appInfo = (await getCustomizedAppInfoByRelateId(_version.relateId, _workspaceId)).data;
      }
    }, {
      immediate: true,
    });


    this.$watch(() => {
      return {
        version: this.version,
        collocation: this.collocation,
      };
    }, ({ version: _version, collocation: _collocation }) => {
      this.keyframe = undefined;
      if (_version && _collocation) {
        if (_collocation.threeDimensional) {
          this.keyframe = initGestureFrame(_collocation);
        } else {
          this.keyframe = null;
        }
      }
    }, {
      immediate: true,
    });

    this.$watch(() => {
      return {
        collocation: this.collocation,
      };
    }, ({ collocation: _collocation }) => {
      this.preview = undefined;
      if (_collocation) {
        [this.preview] = _collocation.contentThumbnails;
      }
    }, {
      immediate: true,
    });

    this.$watch(() => this.preview, async (preview) => {
      if (preview) {
        console.log(preview);
        this.previewWithSign = await signUrl(preview);
        console.log(this.previewWithSign);
      } else {
        this.previewWithSign = null;
      }
    });
  },
  data() {
    return {
      /** @type {undefined|Version} */
      version: undefined,
      /** @type {undefined|CustomizedAppInfo} */
      appInfo: undefined,
      /** @type {undefined|CatalogForTradeFair[]} */
      shoeList: undefined,
      /** @type {undefined|GestureFrameOptions} */
      skuList: [],
      skuIndex: 1,
      keyframe: undefined,
      /** @type {undefined|Collocation} */
      collocation: undefined,
      /** @type {undefined|string} */
      preview: undefined,
      /** @type {undefined|string} */
      previewWithSign: undefined,
      /** @type {CatalogForTradeFair} */
      currentShoeOfShoeList: undefined,
      canAddWish: undefined,
      isShowKeyframe: false,
      isShowComment: true,
      skuPickerCanScrollDown: true,
      skuPickerCanScrollUp: true,
    };
  },
});
</script>
<style lang="scss" scoped>
