  margin: 15px 20px 0;
  position: relative;
  overflow: hidden;
  :fullscreen & {
    margin: 54px;
    margin-right: 20px;
  }
}

.detail-left {
  margin-left: 30px;
  @media (max-width: 1366px) {
    margin-left: 0;
  }
}
