.preview {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute;
  :fullscreen & {
    top: 93px;
    left: 80px;
    right: 80px;
    bottom: 78px;
  }
}
.preview {
  cursor: pointer;
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
}

.shoe-viewer {
  flex: 1;
  position: relative;
  .indicator {
    z-index: 10;
    font-size: 18px;
    line-height: 27px;
    top: 64px;
    left: 50%;
    position: absolute;
    transform: translateX(-50%);
    display: none;
    :fullscreen & {
      display: block;
    }
  }
  .btn-arrow-left,
  .btn-arrow-right {
    text-align: center;
    cursor: pointer;
    font-size: 50px;
    z-index: 10;
    top: 50%;
    width: 80px;
    height: 80px;
    line-height: 80px;
    transition: translateY(-50%);
    position: absolute;
    display: none;
    :fullscreen & {
      display: block;
    }
  }
  .btn-arrow-left {
    left: 0;
  }
  .btn-arrow-right {
    right: 0;
  }
  .detail-header {
    position: absolute;
    z-index: 10;
    display: none;
    :fullscreen & {
      display: block;
    }
  }
  .thumb-picker {
    display: none;
    position: absolute;
    z-index: 10;
    bottom: 10px;
    left: 0;
    right: 56px;
    :fullscreen & {
      display: block;
    }
  }
  .btn-group {
    position: absolute;
    display: flex;
    &.top-right {
      top: 0;
      right: 0;
    }
    &.bottom-right {
      right: 0;
      bottom: 0;
    }
    > {
      [class^="btn-"],
      [class*=" btn-"] {
        color: #03234c;
        border-radius: 50%;
        background-color: rgba(255, 255, 255, 0.7);
        margin: 10px 18px;
        width: 50px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        cursor: pointer;
        z-index: 2;
        i {
          line-height: 50px;
          font-size: 35px;
        }
        &:hover {
          color: rgba($color: #03234c, $alpha: 0.7);
        }
      }
    }
  }
}

.btn-add-to-wish,
.btn-3d {
  position: relative;
  &:hover {
    &::after {
      position: absolute;
      font-size: 12px;
      background-color: #03234c;
      height: 22px;
