<template>
  <column>
    <el-popover v-if="isEmojiSupport" trigger="hover" v-model="emojiVisible">
      <EmojiSelector style="height: 250px; width: 250px" @input="insertEmoji" />
      <span slot="reference" class="iconfont-wrapper">
        <i class="iconfont iconbiaoqing"></i>
      </span>
    </el-popover>
    <row style="border-bottom: 1px solid #ddd;padding-bottom:5px;">
      <row flex='1' class="input-wrapper">
        <el-input type="textarea" style="width: 100%;" maxlength="200"
          v-model="message" rows="1" :placeholder="$t('order-meeting.detail.comment_placeholder')"
          @input="handleInput" ref="comment"></el-input>
      </row>
      <column class="flex-justify-flex-end">
        <FilaButton
          mode="reverse"
          type="medium"
          @click="handleClickComment"
          :disabled="!message">{{$t('order-meeting.detail.comment')}}</FilaButton>
      </column>
    </row>
  </column>
</template>

<script>
import i18n from 'i18next';
import {
  AddComment,
  // EditComment,
} from '@/api/comment';
import EmojiSelector from '@/components/emoji/Selector.vue';
import FilaButton from '@/views/order-meeting/components/BaseButton';
import isEmojiSupport from '_/is-emoji-support';
import toast from '@/components/ToastTipWrapper';

export default {
  props: {
    objectId: { type: String },
    workspaceId: { type: String },
  },
  components: {
    EmojiSelector,
    FilaButton,
  },
  data() {
    return {
      emojiVisible: false,
      message: '',
      isEmojiSupport: isEmojiSupport(),
    };
  },
  methods: {
    insertEmoji(char) {
      this.message = `${this.message}${char}`;
      this.emojiVisible = false;
      this.$refs.comment.focus();
    },
    async handleClickComment() {
      try {
        await AddComment(this.workspaceId, {
          objectId: this.objectId,
          content: this.message,
          versionName: '',
          versionId: null,
          imgs: [],
          at: [],
        });
        this.$emit('on-add-comment');
        this.message = '';
      } catch (e) {
        this.$message.error(e.message);
      }
    },
    async handleInput() {
      const target = this.$refs.comment.$el.children[0];
      if (target.scrollHeight < 90) {
        target.style.height = `${target.scrollHeight}px`;
      }
      if (this.message.length >= 200) {
        await toast(' ', i18n.t('exceeds_charater_limit'));
      }
    },
  },
};
</script>

