<template>
  <section style="position: relative;">
    <Scrollable x class="scrollable product-list" :hideScrollbar="true" :class="size"
      ref="product-list" @scroll='updateScrollable'>
      <row>
        <column
          class="item"
          :class="{current: item === value}"
          v-for="item in items" :key="item.id" flex="none"
          @click="$emit('clickItem', item)">
          <AspectWidth :aspect="1">
            <BaseImage fit="scale-down" :src="item.contentThumbnail" class="image">
              <template v-slot:error>
                <div class="img-error">
                  <img src="@/assets/img/public/default-image.png"
                    alt="error" style="width: 100%;height:100%" />
                </div>
              </template>
            </BaseImage>
          </AspectWidth>
          <div class="name"><span>{{item.name}}</span></div>
        </column>
      </row>
    </Scrollable>
    <div class="scroll-left" @click="scrollRow(1, 1)" v-if="canScrollLeft">
      <i class="iconfont icon-angle-left" style="font-size: 28px;"></i>
    </div>
    <div class="scroll-right" @click="scrollRow(0, 1)" v-if="canScrollRight">
      <i class="iconfont icon-angle-right" style="font-size: 28px;"></i>
    </div>
  </section>
</template>
<script>
import Vue from 'vue';

import Scrollable from '@/components/Scrollable';
import BaseImage from '@/components/base/Image';
import AspectWidth from '@/components/AspectWidth';

export default Vue.extend({
  components: {
    BaseImage,
    Scrollable,
    AspectWidth,
  },
  model: {
    value: 'value',
    event: 'clickItem',
  },
  props: {
    size: {

    },
    value: {},
    items: {},
    name: String,
  },
  data() {
    return {
      canScrollLeft: null,
      canScrollRight: null,
    };
  },
  mounted() {
    const target = this.$refs['product-list'].$el.children[0];

    // 等渲染完成，才计算是否显示scorll参数
    setTimeout(() => {
      this.updateScrollable({ target });
    }, 500);
  },
  methods: {
    async scrollRow(dir = 0 /** num = 2* */) {
      // eslint-disable-next-line no-unused-vars
      const target = this.$refs['product-list'].$el.children[0];
      if (!target.scrollTo) {
        if (!dir) {
          target.scrollLeft += target.clientWidth;
        } else {
          target.scrollLeft -= target.clientWidth;
        }
        return;
      }
      if (!dir) {
        target.scrollTo({
          left: target.scrollLeft + target.clientWidth,
          behavior: 'smooth',
        });
      } else {
        target.scrollTo({
          left: target.scrollLeft - target.clientWidth,
          behavior: 'smooth',
        });
      }
    },
    updateScrollable({ target }) {
      /** @type {HTMLDivElement} */
      this.canScrollLeft = target.scrollLeft !== 0;
      this.canScrollRight = target.scrollLeft + target.clientWidth !== target.scrollWidth;
    },
  },
});

</script>
