    cursor: pointer;
    .name {
      margin: 0 10px;
      margin-top: 10px;
      min-height: 20px;
      position: relative;
      > span {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        text-overflow: ellipsis;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        display: block;
      }
    }
    + .item {
      margin-left: 15px;
    }
    &:hover {
      border-color: #000;
    }
    &.current,
    &:active {
      border-color: black;
      border-width: 2px;
      padding: 0;
      padding-bottom: 10px;
    }
  }

  /deep/ .item {
    .el-image{
      border-width: 2px;
      .el-image__inner{
        height: auto;
        position: absolute;
        bottom: 0;
      }
    }
  }
}
